import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';

const AboutUsPage = () => {

    // Animation on scroll
    useEffect(() => {
        const observer = new IntersectionObserver((entries) => {
            entries.forEach(entry => {
                if (entry.isIntersecting) {
                    entry.target.classList.add('is-visible');
                }
            });
        }, { threshold: 0.1 });

        const sections = document.querySelectorAll('.fade-in-section');
        sections.forEach(section => observer.observe(section));

        return () => sections.forEach(section => observer.unobserve(section));
    }, []);

    return (
        <>
            {/* Hero Section */}
            <section className="py-5 text-center about-hero fade-in-section">
                <div className="container">
                    <h1 className="display-3">Pasja. Precyzja. Perfekcja.</h1>
                    <p className="lead text-muted">Poznaj filozofię, która stoi za każdym naszym wydrukiem.</p>
                </div>
            </section>

            {/* Our Story Section */}
            <section className="py-5 fade-in-section">
                <div className="container">
                    <div className="row justify-content-center">
                        <div className="col-lg-10">
                            <h2 className="text-center mb-5">Nasza Historia</h2>
                            <p className="lead text-muted drop-cap">Witaj w Rex3D, miejscu, które narodziło się z autentycznej fascynacji nieograniczonymi możliwościami druku 3D. To, co zaczęło się jako hobby i setki godzin eksperymentów, szybko przerodziło się w misję: dostarczanie produktów, które łączą w sobie technologiczną precyzję z ludzką potrzebą piękna. Nie jesteśmy wielką fabryką – jesteśmy pracownią, w której każdy projekt traktowany jest indywidualnie, z najwyższą starannością.</p>
                            <p className="text-muted">Wierzymy, że najlepsze rzeczy powstają z połączenia wiedzy i pasji. Dlatego nieustannie doskonalimy nasze umiejętności i park maszynowy, aby móc realizować coraz bardziej złożone i wymagające projekty. Naszym celem jest nie tylko drukowanie obiektów, ale tworzenie wartości – dla Twojego domu, Twojej pracy i Twoich pasji.</p>
                        </div>
                    </div>
                </div>
            </section>

            {/* Our Philosophy Section */}
            <section id="philosophy" className="py-5 bg-light fade-in-section">
                <div className="container">
                    <h2 className="section-title">Nasza Filozofia</h2>
                    <div className="row text-center g-4">
                        <div className="col-lg-4">
                            <div className="philosophy-card">
                                <div className="icon"><i className="bi bi-vector-pen"></i></div>
                                <h3>Design w Centrum</h3>
                                <p className="text-muted">Każdy obiekt, który tworzymy, musi być nie tylko funkcjonalny, ale i estetyczny. Design to dla nas język, którym opowiadamy historię każdego produktu.</p>
                            </div>
                        </div>
                        <div className="col-lg-4">
                            <div className="philosophy-card">
                                <div className="icon"><i className="bi bi-gear-wide-connected"></i></div>
                                <h3>Mistrzostwo Technologii</h3>
                                <p className="text-muted">Nieustannie optymalizujemy procesy i ustawienia naszych drukarek, aby osiągnąć maksymalną precyzję i powtarzalność, niezależnie od skomplikowania modelu.</p>
                            </div>
                        </div>
                        <div className="col-lg-4">
                            <div className="philosophy-card">
                                <div className="icon"><i className="bi bi-recycle"></i></div>
                                <h3>Odpowiedzialne Materiały</h3>
                                <p className="text-muted">Świadomie wybieramy dostawców i materiały, stawiając na te, które są trwałe, bezpieczne i w miarę możliwości przyjazne dla środowiska, jak biodegradowalny PLA.</p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

            {/* CTA Section */}
            <section className="py-5 text-center container fade-in-section">
                <div className="row">
                    <div className="col-lg-8 mx-auto">
                        <h2 className="fw-light">Porozmawiajmy o Twoim Pomyśle</h2>
                        <p className="lead text-muted">Niezależnie od tego, czy masz gotowy projekt, czy tylko wstępną wizję, jesteśmy tutaj, aby pomóc Ci ją zrealizować.</p>
                        <p>
                            <Link to="/contact" className="btn btn-primary my-2">Skontaktuj się z nami</Link>
                            <Link to="/products" className="btn btn-outline-dark my-2 ms-2">Zobacz gotowe produkty</Link>
                        </p>
                    </div>
                </div>
            </section>
        </>
    );
};

export default AboutUsPage;
